import React, {HTMLAttributes} from "react";
import {Menu, Styles} from "@dashthis/ui";
import {webViewZoomToolBarButtonStyle} from "../styles/webViewZoomToolBarButton.style";
import {Icon} from "../../../components";
import WebViewZoomToolBarButtonMenu from "./WebViewZoomToolBarButtonMenu";

interface IProps extends HTMLAttributes<HTMLDivElement> {
  /**
   *
   */
  onZoomIn: () => void,
  /**
   *
   */
  onZoomOut: () => void,
  /**
   *
   */
  zoomFactor: number,
  /**
   *
   */
  setZoomFactor: (value: number) => void
}

export const WebViewZoomToolBarButton = (props: IProps) => {
  const {onZoomIn, onZoomOut, zoomFactor, setZoomFactor, ...rest} = props;
  const css = Styles.Hooks.useClassNameWithThoseOfProps(props, webViewZoomToolBarButtonStyle);

  return (
    <div {...rest} className={css}>
      <Icon.Icon onClick={onZoomOut} icon={Icon.Enums.Icons.WebViewMinus}/>
      <Menu.MenuPopper reference={<span>{Math.floor(zoomFactor * 100)}%</span>}>
        {({hideMenu}) => (<WebViewZoomToolBarButtonMenu zoomFactor={zoomFactor} setZoomFactor={setZoomFactor} closeMenu={hideMenu}/>)}
      </Menu.MenuPopper>
      <Icon.Icon onClick={onZoomIn} icon={Icon.Enums.Icons.WebViewPlus}/>
    </div>
  );
};

WebViewZoomToolBarButton.displayName = "Components/WebView/Components/WebViewZoomToolBarButton";

export default WebViewZoomToolBarButton;
