import {createContext} from "react";
import {noop} from "lodash";
import {IEventService} from "../eventService/types";

export const EventServiceContext = createContext<IEventService>({
  onDownloadFromUrlComplete: () => noop,
  on: noop,
  once: noop,
  removeListener: noop,
  sendShowContextMenuForWebView: noop,
  send: noop,
  sendRequestEmulateWindow: noop,
  sendDownloadFromUrl: noop,
  onContextMenuForWebViewInspectElementClick: () => noop,
  onDownloadFromUrlFail: () => noop,
  onExportDashboardToPdfComplete: () => noop,
  onExportDashboardToPdfError: () => noop
});
