import Electron, {ipcRenderer} from "electron";
import {IEventService} from "./types";
import {Enums} from "@dashthis/common";
import {Enums as StudioEnum} from "@dashthis/studio-common";

type ListenerEventHandler = (event: Electron.IpcRendererEvent, ...args: any[]) => void;

export const electronEventService: IEventService<Electron.IpcRendererEvent> = {
  onExportDashboardToPdfComplete(callback: (data: Uint8Array) => void) {
    const listener: ListenerEventHandler = (event, data) => {
      callback(data);
    };

    ipcRenderer.once(StudioEnum.IPCChannelEnum.ExportDashboardToPdfComplete, listener);

    return () => {
      ipcRenderer.removeListener(StudioEnum.IPCChannelEnum.ExportDashboardToPdfComplete, listener);
    };
  },
  onExportDashboardToPdfError(callback: (message: string) => void) {
    const listener: ListenerEventHandler = (event, message) => {
      callback(message);
    };

    ipcRenderer.once(StudioEnum.IPCChannelEnum.ExportDashboardToPdfError, listener);

    return () => {
      ipcRenderer.removeListener(StudioEnum.IPCChannelEnum.ExportDashboardToPdfError, listener);
    };
  },
  onContextMenuForWebViewInspectElementClick (callback: (x: number, y: number) => void) {
    const listener: ListenerEventHandler = (event, x, y) => {
      callback(x, y);
    };

    ipcRenderer.on(StudioEnum.IPCChannelEnum.ContextMenuActionInspectElement, listener);

    return () => {
      ipcRenderer.removeListener(StudioEnum.IPCChannelEnum.ContextMenuActionInspectElement, listener);
    };
  },
  onDownloadFromUrlComplete(callback: (file: string) => void) {
    const listener: ListenerEventHandler = (event, file) => {
      callback(file);
    };

    ipcRenderer.once(StudioEnum.IPCChannelEnum.DownloadFromUrlComplete, listener);

    return () => {
      ipcRenderer.removeListener(StudioEnum.IPCChannelEnum.DownloadFromUrlComplete, listener);
    };
  },
  onDownloadFromUrlFail(callback: (message: string) => void) {
    const listener: ListenerEventHandler = (event, message) => {
      callback(message);
    };

    ipcRenderer.once(StudioEnum.IPCChannelEnum.DownloadFromUrlFail, listener);

    return () => {
      ipcRenderer.removeListener(StudioEnum.IPCChannelEnum.DownloadFromUrlFail, listener);
    };
  },
  sendRequestEmulateWindow(url: string, id: string, email: string, env: Enums.EnvsEnum) {
    ipcRenderer.send(StudioEnum.IPCChannelEnum.RequestEmulateWindow, url, id, email, env);
  },
  sendDownloadFromUrl: (url: string) => {
    ipcRenderer.send(StudioEnum.IPCChannelEnum.DownloadFromUrl, url);
  },
  sendShowContextMenuForWebView: (x, y, editFlags) => {
    ipcRenderer.send(StudioEnum.IPCChannelEnum.ShowContextMenuForWebView, x, y, editFlags);
  },
  on(channel, listener) {
    ipcRenderer.on(channel, listener);
  },
  once(channel, listener) {
    ipcRenderer.once(channel, listener);
  },
  removeListener(channel, listener) {
    ipcRenderer.removeListener(channel, listener);
  },
  send(channel, ...args) {
    ipcRenderer.send(channel, ...args);
  }
};