import React, {ButtonHTMLAttributes} from "react";
import {Styles, Tooltip} from "@dashthis/ui";
import {webViewToolbarButtonDisabledStyle, webViewToolbarButtonStyle} from "../styles/webViewToolbar.style";
import {Icon} from "../../index";
import {Conditional} from "@dashthis/common";

interface IProps extends ButtonHTMLAttributes<HTMLButtonElement> {
  /**
   * Icon for button.
   */
  icon: Icon.Enums.Icons,
  /**
   * Add tooltip on button
   */
  tooltip?: string
}

export const WebViewToolBarButton = (props: IProps) => {
  const {icon, disabled, tooltip, ...rest} = props;
  const css = Styles.Hooks.useClassNameWithThoseOfProps(props,
    webViewToolbarButtonStyle,
    Conditional.is(disabled!, webViewToolbarButtonDisabledStyle)
  );
  const toolbarButton = (
    <button {...rest} className={css}>
      <Icon.Icon icon={icon}/>
    </button>
  );

  if (tooltip != null) {
    return (
      <Tooltip.Tooltip config={{
        placement: Tooltip.Enums.TooltipPlacementEnum.Bottom,
        offset: [0, 10]
      }} mode={Tooltip.Enums.TooltipShowModeEnum.OnMouseOver} Reference={toolbarButton}>
        {tooltip}
      </Tooltip.Tooltip>
    );
  } else {
    return toolbarButton;
  }
};

WebViewToolBarButton.displayName = "Components/WebView/Components/WebViewToolBarButton";

export default WebViewToolBarButton;
