import {AppSettingStorage, EmulateSettings} from "./types";

export interface IAppSettingService {
  setEmulateSettings(setting: EmulateSettings): EmulateSettings;

  getEmulateSettings(): EmulateSettings
}

export const appSettingService = (storage: AppSettingStorage): IAppSettingService => ({

  setEmulateSettings(setting) {
    storage.set("emulateSettings", setting);

    return setting;
  },

  getEmulateSettings() {
    return storage.get("emulateSettings", {
      email: "",
      password: ""
    });
  }
});