import {StudioCSS} from "../../../styles/types";

export const inputStyle: StudioCSS = (theme, utilities) => ({
  label: "input"
});

export const darkInputStyle: StudioCSS = (theme, utilities) => ({
  label: "dark-input",
  "&[class*=input]": {
    backgroundColor: utilities.colors("mineShaft").alpha(.7).toString(),
    borderColor:utilities.colors("scorpion").toString(),
    color: utilities.colors("white").toString(),
    "&::placeholder": {
      color: utilities.colors("white").alpha(.5).toString(),
      ...utilities.fontStyle({
        weight: "n",
        size: "small"
      })
    }
  }
});
