import {StudioCSS} from "../../../styles/types";

export const webViewZoomToolBarButtonStyle: StudioCSS = (theme, utilities) => ({
  label: "web-view-zoom-tool-bar-button",
  display: "grid",
  gridTemplateColumns: "auto 1fr auto",
  height: 15,
  border: "none",
  background: utilities.colors("white").string(),
  padding: `${utilities.space("t")} ${utilities.space("s")}`,
  alignItems: "center",
  gap: utilities.space("s"),
  boxSizing: "content-box",
  borderRadius: 2,
  svg: {
    width: 10,
    fill: utilities.colors("primary").string(),
    ":hover": {
      cursor: "pointer"
    }
  },
  span: {
    userSelect:"none",
    ":hover": {
      cursor: "pointer"
    },
    ...utilities.fontStyle({
      size: "tiny",
      color: "primary",
      weight: "m"
    })
  }
});
