import React from "react";
import {Input as DefaultInput, Styles} from "@dashthis/ui";
import {inputStyle} from "./styles/input.style";

interface IInputProps extends DefaultInput.Types.IInputProps {
}

export const Input = (props: IInputProps) => {
  const css = Styles.Hooks.useClassNameWithThoseOfProps(props, inputStyle);

  return (
    <DefaultInput.Input  {...props} className={css}/>
  );
};

Input.displayName = "Components/Input";

export default Input;
