import {StudioCSS} from "../../../styles/types";

export const webViewToolbarStyle: StudioCSS = (theme, utilities) => ({
  label: "web-view-toolbar",
  background: utilities.colors("aquaHaze").string(),
  display: "grid",
  gridTemplateColumns: "auto auto auto 1fr auto",
  gap: utilities.space("t"),
  padding: utilities.space("m"),
  alignItems: "center"
});

export const webViewToolbarInputStyle: StudioCSS = (theme, utilities) => ({
  label: "web-view-toolbar-input",
  height: 26,
  background: utilities.colors("white").string(),
  border: "none"
});

export const webViewToolbarInputLoadingStyle: StudioCSS = (theme, utilities) => ({
  label: "web-view-toolbar-input-loading",
  paddingLeft: `${utilities.space("xl")} !important`
});

export const webViewToolbarInputContainerStyle: StudioCSS = (theme, utilities) => ({
  label: "web-view-toolbar-input-container",
  position: "relative",
  display: "inline-flex",
  alignItems: "center"
});

export const webViewToolbarButtonStyle: StudioCSS = (theme, utilities) => ({
  label: "web-view-toolbar-button",
  width: 15,
  height: 15,
  border: "none",
  background: utilities.colors("white").string(),
  padding: utilities.space("t"),
  boxSizing: "content-box",
  borderRadius: 2,
  svg: {
    width: 15,
    height: 15
  },
  ":hover": {
    cursor: "pointer",
    background: utilities.colors("scorpion").string(),
    svg: {
      fill: utilities.colors("white").string()
    }
  }
});

export const webViewToolbarButtonDisabledStyle: StudioCSS = (theme, utilities) => ({
  label: "web-view-toolbar-button-disabled",
  pointerEvents:"none",
  svg: {
    opacity: .3
  }
});

export const webViewToolbarWheelStyle: StudioCSS = (theme, utilities) => ({
  label: "web-view-toolbar-wheel",
  fontSize: 17,
  position:"absolute",
  left: utilities.space("t")
});
