import React, {ReactNode, Reducer, useCallback, useEffect, useReducer, useRef} from "react";
import {NewWindowEvent} from "electron";
import {WebViewToolbar} from "./WebViewToolbar";
import {isNumber, noop} from "lodash";
import {Styles} from "@dashthis/ui";
import {Types} from "@dashthis/studio-common";
import {webViewStyle} from "../styles/webView.style";
import toBuffer from "typedarray-to-buffer";
import {useFileSystemServiceContext} from "../../../services/hooks/useFileSystemServiceContext";
import formatMessage from "format-message";
import {useEventServiceContext} from "../../../services/hooks/useEventServiceContext";


interface IProps {
  /**
   * A String that sets the session used by the page.
   */
  partition: string;
  /**
   * Callback to call when the web view dom are ready.
   */
  onDomReady?: (webView: Types.ElectronTypes.WebviewTag) => void,
  /**
   * The url to load for webview
   */
  url: string,
  /**
   * Callback to call when user click on open in window button.
   */
  onOpenInWindow?: (url: string) => void,
  /**
   * Add extra button on toolbar
   */
  extraButtons?: ReactNode[],
  /**
   * Indicate if the download PDF button are visible
   */
  canDownloadPDF?: boolean;
  /**
   * Callback to call to how generate pdf.
   */
  generatePdf?: (webView: Types.ElectronTypes.WebviewTag) => Promise<Uint8Array>
}

type InitialState = {
  url: string,
  canGoBack: boolean,
  canGoForward: boolean,
  isLoading: boolean,
  zoomFactor: number
}
type SetUrl = {
  type: "setUrl",
  url: string
}
type SetZoomFactor = {
  type: "setZoomFactor",
  action: "+" | "-" | number
}
type SetCanGoBackOrForward = {
  type: "setCanGoBackOrForward",
  back: boolean,
  forward: boolean
}
type SetIsLoading = {
  type: "setIsLoading",
  value: boolean
}
type ReducerAction = SetUrl | SetCanGoBackOrForward | SetIsLoading | SetZoomFactor;

const initialState: InitialState = {
  url: "",
  canGoBack: false,
  canGoForward: false,
  isLoading: false,
  zoomFactor: 1
};
const reducer = (state: InitialState, action: ReducerAction): InitialState => {
  switch (action.type) {
    case "setZoomFactor": {
      let zoomFactor = 1;

      if (isNumber(action.action as number)) {
        zoomFactor = action.action as number;
      } else {
        zoomFactor = action.action === "+" ? state.zoomFactor + .1 : state.zoomFactor - .1;
      }

      return {
        ...state,
        zoomFactor
      };
    }
    case "setIsLoading":
      return {
        ...state,
        isLoading: action.value
      };
    case "setUrl":
      return {
        ...state,
        url: action.url
      };
    case "setCanGoBackOrForward":
      return {
        ...state,
        canGoBack: action.back,
        canGoForward: action.forward
      };
  }
};
const defaultGeneratePdf = async (webView: Types.ElectronTypes.WebviewTag): Promise<Uint8Array> => {
  return await webView.printToPDF({
    printBackground: true
  });
};

export const WebView = (props: IProps) => {
  const fileSystemService = useFileSystemServiceContext();
  const {partition, onDomReady, url, onOpenInWindow, extraButtons, canDownloadPDF = false, generatePdf = defaultGeneratePdf} = props;
  const css = Styles.Hooks.useClassName(webViewStyle);
  const {sendShowContextMenuForWebView, onContextMenuForWebViewInspectElementClick} = useEventServiceContext();
  const zoomFactor = useRef<number>(1);
  const [state, dispatch] = useReducer<Reducer<InitialState, ReducerAction>>(reducer, initialState);

  zoomFactor.current = state.zoomFactor;

  const ref = useRef<HTMLDivElement | null>(null);
  const webView = useRef<Types.ElectronTypes.WebviewTag | null>(null);
  const onWebViewDidStartLoading = useCallback(() => {
    dispatch({
      type: "setIsLoading",
      value: true
    });
  }, [dispatch]);
  const onWebViewDidStopLoading = useCallback(() => {
    dispatch({
      type: "setIsLoading",
      value: false
    });
  }, [dispatch]);
  const setZoomFactor = useCallback((value: number) => {
    dispatch({
      type: "setZoomFactor",
      action: value
    });
  }, [dispatch]);
  const onWebViewDidNavigate = useCallback((event) => {
    dispatch({
      type: "setUrl",
      url: event.url
    });
    dispatch({
      type: "setCanGoBackOrForward",
      back: webView.current!.canGoBack(),
      forward: webView.current!.canGoForward()
    });
  }, [dispatch]);
  const onContextMenu = useCallback((event: Types.ElectronTypes.WebviewTagContextMenuEvent) => {
    sendShowContextMenuForWebView(event.params.x, event.params.y, event.params.editFlags)
  }, [sendShowContextMenuForWebView]);
  const onNewWindow = useCallback((event: NewWindowEvent) => onOpenInWindow?.(event.url), [onOpenInWindow]);
  const onWebViewDomReady = useCallback(() => {
    webView.current!.setZoomFactor(state.zoomFactor);
    webView.current!.blur();
    webView.current!.focus();
    onDomReady?.(webView.current!);
  }, []);
  const onDidFinishLoad = useCallback(() => {
    if (webView.current != null) {
      webView.current.setZoomFactor(zoomFactor.current);
    }
  }, [webView, zoomFactor]);
  const handleDownloadAsPDF = async () => {
    const pdfData = await generatePdf(webView.current!);
    const path = await fileSystemService.showSaveDialog({
      defaultPath: formatMessage("untitled.pdf")
    });

    if (path != null) {
      await fileSystemService.write(path, toBuffer(pdfData));
    }
  };
  const handleOpenInWindow = onOpenInWindow != null ? () => onOpenInWindow(state.url) : undefined;

  useEffect(() => {
    webView.current?.loadURL?.(url);
  }, [url]);

  useEffect(() => {
    webView.current?.setZoomFactor?.(state.zoomFactor);
  }, [state.zoomFactor]);

  useEffect(() => {
    const removeInspectElementListener = onContextMenuForWebViewInspectElementClick((x, y) => webView.current?.inspectElement(x, y));

    if (ref.current != null) {
      ref.current.innerHTML = `<webview partition="persist:${partition}" style="display:inline-flex; width:100%; height:100%" id="foo" src="${url}"/>`;
      webView.current = ref.current.querySelector("webview");
      if (webView.current != null) {
        webView.current.addEventListener("did-start-loading", onWebViewDidStartLoading);
        webView.current.addEventListener("did-stop-loading", onWebViewDidStopLoading);
        webView.current.addEventListener("did-navigate", onWebViewDidNavigate);
        webView.current.addEventListener("dom-ready", onWebViewDomReady);
        webView.current.addEventListener("new-window", onNewWindow);
        webView.current.addEventListener("context-menu", onContextMenu);
        webView.current.addEventListener("did-finish-load", onDidFinishLoad);
      }
    }

    return () => {
      removeInspectElementListener();
      if (webView.current != null) {
        webView.current.removeEventListener("did-start-loading", onWebViewDidStartLoading);
        webView.current.removeEventListener("did-stop-loading", onWebViewDidStopLoading);
        webView.current.removeEventListener("did-navigate", onWebViewDidNavigate);
        webView.current.removeEventListener("dom-ready", onWebViewDomReady);
        webView.current.removeEventListener("new-window", onNewWindow);
        webView.current.removeEventListener("did-finish-load", onDidFinishLoad);
      }
    };

  }, [ref, webView]);

  return (
    <div className={css}>
      <WebViewToolbar
        setZoomFactor={setZoomFactor}
        canDownloadPDF={canDownloadPDF}
        onDownloadPdfClick={handleDownloadAsPDF}
        extraButtons={extraButtons}
        onReload={webView.current != null ? () => {
          webView.current!.reload();
        } : noop}
        onBack={webView.current != null ? () => {
          webView.current!.goBack();
        } : noop}
        onForward={webView.current != null ? () => {
          webView.current!.goForward();
        } : noop}
        canReload={!state.isLoading}
        canGoForward={state.canGoForward}
        canGoBack={state.canGoBack}
        isLoading={state.isLoading}
        onZoomOut={webView.current != null ? () => {
          dispatch({
            type: "setZoomFactor",
            action: "-"
          });
        } : noop}
        onZoomIn={webView.current != null ? () => {
          dispatch({
            type: "setZoomFactor",
            action: "+"
          });
        } : noop}
        onOpenInWindow={handleOpenInWindow}
        url={state.url}
        zoomFactor={state.zoomFactor}
      />
      <div ref={ref}/>
    </div>
  );
};

WebView.displayName = "Components/WebView/Components/WebView";

export default WebView;
