import React from "react";
import ReactDOM from "react-dom";
import {Styles} from "@dashthis/ui";
import {studioTheme} from "../styles/studioTheme";
import Emulate from "../app/Emulate";
import {EventServiceContext} from "../services/context/EventServiceContext";
import {electronEventService} from "../services/eventService/electronEventService";
import {OpenUrlServiceContext} from "../services/context/OpenUrlServiceContext";
import {electronOpenUrlService} from "../services/openUrlService/electronOpenUrlService";


Styles.globalThemeProvider.setTheme(studioTheme);

ReactDOM.render(
  <Styles.UiThemeProvider>
    <EventServiceContext.Provider value={electronEventService}>
      <OpenUrlServiceContext.Provider value={electronOpenUrlService}>
        <Emulate/>
      </OpenUrlServiceContext.Provider>
    </EventServiceContext.Provider>
  </Styles.UiThemeProvider>
  , document.getElementById("root"));