import {createContext} from "react";
import {IFileSystemService} from "../fileSystemService/types";

export const FileSystemServiceContext = createContext<IFileSystemService>({
  read<T = string>(path: string, returnAs: "utf8" | "buffer" | "json" | "jsonWithDates"): Promise<T> {
    return Promise.resolve("") as any;
  },
  write(path: string, data: string | Buffer | object | any[]): Promise<void> {
    return Promise.resolve();
  },
  showSaveDialog(options): Promise<string | undefined> {
    return Promise.resolve("");
  }
});
