import React from "react";
import {Menu as DefaultMenu, Styles} from "@dashthis/ui";
import {menuStyle} from "./styles/menu.style";

interface IProps extends DefaultMenu.Types.IMenuProps {
}

export const Menu = (props: IProps) => {
  const css = Styles.Hooks.useClassNameWithThoseOfProps(props, menuStyle);

  return (
    <DefaultMenu.Menu {...props} className={css}/>
  );
};

Menu.displayName = "";

export default Menu;
