import {Enums, Storage} from "@dashthis/common";
import {IStudioBrowserWindow, StudioConfig, StudioConfigStorageData} from "@dashthis/studio-electron";
import {getCurrentWindow} from "@electron/remote";

const getConfig = (env: Enums.EnvsEnum): StudioConfig => {
  const window = getCurrentWindow() as IStudioBrowserWindow;

  return window.getConfigForEnv(env);
};

export const configWriterReader = (env: Enums.EnvsEnum): Storage.IStorageDataWriterReader<StudioConfigStorageData> => ({
  set(key, value) {
    const config = getConfig(env);

    config.set(key, value);
  },

  delete(_key) {
    throw new Error("Method not implemented");
  },

  clear() {
    throw new Error("Method not implemented");
  },

  get(key, defaultValue) {
    const config = getConfig(env);

    return config.get(key, defaultValue);
  }
});