import React from "react";
import {Menu} from "../../../components";
import {Menu as DefaultMenu} from "@dashthis/ui";
import formatMessage from "format-message";

interface IProps {
  /**
   * Callback to close the menu
   */
  closeMenu: () => void,
  /**
   * Callback to change the zoom factor
   */
  setZoomFactor: (value: number) => void,
  /**
   * The current zoom factor
   */
  zoomFactor: number,
}


export const WebViewZoomToolBarButtonMenu = (props: IProps) => {
  const {closeMenu, setZoomFactor, zoomFactor} = props;
  const handleMenuItemClick = (factor: number) => () => {
    setZoomFactor(factor);
    closeMenu();
  };

  return (
    <Menu.Menu>
      <DefaultMenu.MenuItem disabled={zoomFactor === 1} onClick={handleMenuItemClick(1)}>{formatMessage("Reset")}</DefaultMenu.MenuItem>
      <DefaultMenu.MenuSeparator/>
      <DefaultMenu.MenuItem onClick={handleMenuItemClick(0.5)}>{formatMessage("50%")}</DefaultMenu.MenuItem>
      <DefaultMenu.MenuItem onClick={handleMenuItemClick(1)}>{formatMessage("100%")}</DefaultMenu.MenuItem>
      <DefaultMenu.MenuItem onClick={handleMenuItemClick(2)}>{formatMessage("200%")}</DefaultMenu.MenuItem>
    </Menu.Menu>
  );
};

WebViewZoomToolBarButtonMenu.displayName = "Components/WebView/Components/WebViewZoomToolBarButtonMenu";


export default WebViewZoomToolBarButtonMenu;
