import React, {ReactNode} from "react";
import {WebView} from "../components";
import {getCurrentWindow} from "@electron/remote";
import {IStudioEmulateBrowserWindow} from "@dashthis/studio-electron/dist/app/WindowManager";
import {Styles} from "@dashthis/ui";
import {emulateStyle} from "./styles/emulate.style";
import {useEventServiceContext} from "../services/hooks/useEventServiceContext";
import {Enums as CommonEnums} from "@dashthis/common";
import {v4 as uuid} from "uuid";
import {appSettingService} from "../services/appSettings/appSettingService";
import {appSettingStorage} from "../services/appSettings/appSettingStorage";
import {configWriterReader} from "../storages/configWriterReader";
import {useEndpointsServiceContext} from "../services/hooks/useEndpointsServiceContext";
import {Types} from "@dashthis/studio-common";

interface IProps {
  /**
   * Component children.
   */
  children?: ReactNode;
}

const getConfig = () => {
  const window = getCurrentWindow() as IStudioEmulateBrowserWindow;

  return window.emulateConfig;
};

export const Emulate = (props: IProps) => {
  const config = getConfig();
  const css = Styles.Hooks.useClassName(emulateStyle);
  const eventService = useEventServiceContext();
  const endpoint = useEndpointsServiceContext();
  const onOpenInWindow = (url?: string) => {
    eventService.sendRequestEmulateWindow(url != null ? url : config!.url, uuid(), config!.email, endpoint.for);
  };
  const asService = appSettingService(appSettingStorage(configWriterReader(config!.env as CommonEnums.EnvsEnum)));
  const onDomReady = async (view: Types.ElectronTypes.WebviewTag) => {
    const emulateSettings = asService.getEmulateSettings();

    if (emulateSettings.password !== "" && emulateSettings.email !== "") {
      await view.executeJavaScript(`
        if(document.getElementById('Email') != null){
            document.getElementById('Email').value="${emulateSettings.email}";
            document.getElementById('Password').value="${emulateSettings.password}";
            document.getElementsByTagName("form")[0].submit();
        }
    `);
    }
  };

  return (
    <div className={css}>
      <WebView.WebView onDomReady={onDomReady} onOpenInWindow={onOpenInWindow} partition={config!.email} url={config!.url}/>
    </div>
  );
};

Emulate.displayName = "App/Emulate";

export default Emulate;
