import React, {ReactNode} from "react";
import {Spinner, Styles} from "@dashthis/ui";
import {webViewToolbarInputContainerStyle, webViewToolbarInputLoadingStyle, webViewToolbarInputStyle, webViewToolbarStyle, webViewToolbarWheelStyle} from "../styles/webViewToolbar.style";
import WebViewToolBarButton from "./WebViewToolBarButton";
import {Enums} from "../../Icon";
import {Input} from "../../index";
import {repeat} from "lodash";
import formatMessage from "format-message";
import {Conditional} from "@dashthis/common";
import WebViewZoomToolBarButton from "./WebViewZoomToolBarButton";

interface IProps {
  /**
   * The url to display in input
   */
  url: string,
  /**
   * Indicate if the button go back are enabled.
   */
  canGoBack: boolean,
  /**
   * Indicate if the button go forward are enabled.
   */
  canGoForward: boolean,
  /**
   * Indicate if the button reload are enabled.
   */
  canReload: boolean,
  /**
   * Indicate if the button download PDF are visible.
   */
  canDownloadPDF?: boolean,
  /**
   * Call back to call when button reload are clicked.
   */
  onReload: () => void,
  /**
   * Call back to call when button forward are clicked.
   */
  onForward: () => void,
  /**
   * Call back to call when button back are clicked.
   */
  onBack: () => void,
  /**
   * Callback to call when user click on open in window button.
   */
  onOpenInWindow?: () => void,
  /**
   * Callback to call when user click download pdf.
   */
  onDownloadPdfClick?: () => void,
  /**
   * Add extra button on toolbar
   */
  extraButtons?: ReactNode[],
  /**
   * Indicate if the state are in progress.
   */
  isLoading: boolean,
  /**
   * Callback to zoom in.
   */
  onZoomIn: () => void,
  /**
   * Callback to zoom out.
   */
  onZoomOut: () => void,
  /**
   * Callback to change the zoom factor
   */
  setZoomFactor: (value: number) => void,
  /**
   * The current zoom factor
   */
  zoomFactor: number,
}

const getGridTemplateCss = (canOpenInNewWindow: boolean, canDownloadPDF: boolean, extraButtons?: ReactNode[]): string => {
  if (extraButtons == null) {
    return `auto auto auto 1fr auto ${canOpenInNewWindow ? "auto" : ""} ${canDownloadPDF ? "auto" : ""}`;
  } else {
    return `auto auto auto 1fr auto ${canOpenInNewWindow ? "auto" : ""} ${canDownloadPDF ? "auto" : ""} ${repeat("auto ", extraButtons.length)}`;
  }
};

export const WebViewToolbar = (props: IProps) => {
  const {url, canGoBack, canGoForward, onReload, canReload, onForward, onBack, onOpenInWindow, extraButtons, isLoading, onZoomIn, onZoomOut, zoomFactor, setZoomFactor, canDownloadPDF = false, onDownloadPdfClick} = props;
  const wheelCss = Styles.Hooks.useClassName(webViewToolbarWheelStyle);
  const canOpenInNewWindow = onOpenInWindow != null;
  const inputContainerCss = Styles.Hooks.useClassName(webViewToolbarInputContainerStyle);
  const css = Styles.Hooks.useClassName(webViewToolbarStyle, {
    gridTemplateColumns: getGridTemplateCss(canOpenInNewWindow, canDownloadPDF, extraButtons)
  });
  const inputCss = Styles.Hooks.useClassName(
    webViewToolbarInputStyle,
    Conditional.is(isLoading, webViewToolbarInputLoadingStyle)
  );

  return (
    <div className={css}>
      <WebViewToolBarButton disabled={!canGoBack} onClick={onBack} icon={Enums.Icons.WebViewBack}/>
      <WebViewToolBarButton disabled={!canGoForward} onClick={onForward} icon={Enums.Icons.WebViewForward}/>
      <WebViewToolBarButton disabled={!canReload} onClick={onReload} icon={Enums.Icons.WebViewReload}/>
      <div className={inputContainerCss}>
        {isLoading
          ? <Spinner.Wheel className={wheelCss}/>
          : null
        }
        <Input.Input readOnly defaultValue={url} className={inputCss}/>
      </div>
      <WebViewZoomToolBarButton setZoomFactor={setZoomFactor} zoomFactor={zoomFactor} onZoomIn={onZoomIn} onZoomOut={onZoomOut}/>
      {canOpenInNewWindow
        ? <WebViewToolBarButton tooltip={formatMessage("Open in new window")} onClick={onOpenInWindow} icon={Enums.Icons.WebViewOpenInWindow}/>
        : null
      }
      {
        canDownloadPDF
          ? <WebViewToolBarButton tooltip={formatMessage("Download PDF")} onClick={onDownloadPdfClick} icon={Enums.Icons.WebViewDownload}/>
          : null
      }
      {extraButtons}
    </div>
  );
};

WebViewToolbar.displayName = "Components/WebView/Components/WebViewToolbar";

export default WebViewToolbar;
