import {Storage} from "@dashthis/common";
import {AppSettingStorage, AppSettingStorageData, AppSettingStorageDataWriterReader} from "./types";

export const appSettingStorage = (writerReader: AppSettingStorageDataWriterReader): AppSettingStorage => {
  const storage = new Storage.Storage<AppSettingStorageData>(writerReader);

  return storage;
};
